:root {
  --box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Alata", sans-serif;
}

body {
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b4d2e7;
}

.main-container {
  width: 600px;
  background-color: #f8f8f8;
  padding: 40px;
  /* border-radius: 5px; */
  box-shadow: var(--box-shadow);
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}
h3 {
  font-size: 24px;
  color: gray;
}
.todo-form > form {
  display: flex;
}

#add-todo {
  font-size: 24px;
  flex: 9;
  padding-left: 10px;
  border-radius: 0;
  border: none;
  background-color: #b4d2e7;
}
#add-todo:focus {
  outline: none;
}
#submit-todo {
  font-size: 24px;
  flex: 1;
  border: none;
  background-color: #94c5cc;
  padding: 10px;
  cursor: pointer;
}
#submit-todo:focus {
  outline: none;
}
.todos {
  margin-top: 20px;
}

.todos > ul > li {
  list-style-type: none;
}
.todos > ul > li > span {
  width: 100%;
  display: flex;
  align-items: center;
  border-bottom: 2px solid #94c5cc;
  overflow-wrap: break-word;
}
#root > div > div.todos > ul > li > span > p {
  width: 80%;
}
.todo {
  flex: 9;
  display: inline-block;
  font-size: 24px;
  cursor: pointer;
  overflow-wrap: break-word;
}
.todo-done {
  color: gray;
  text-decoration: line-through;
}

.del-todo {
  cursor: pointer;
  border: none;
  flex: 1;
  display: inline-block;
  font-size: 24px;
  background-color: #94c5cc;
  color: black;
  padding: 10px;
  opacity: 0;
}
.del-todo:focus {
  outline: none;
}
.todos li:hover .del-todo {
  opacity: 1;
}
